import React from "react";
import { View } from "@instructure/ui-view";
import Tile from "./Tile";
const CollectionBrowser = ({ collections, onSelectCollection, }) => {
    const renderTiles = () => {
        return collections.map((collection) => {
            const collectionName = collection.name ? collection.name : "My Library";
            return (React.createElement(Tile, { isCollection: true, key: `tile${collection.id}`, id: collection.id, title: collectionName, ownerName: collection.owner.display_name, onSelectCollection: onSelectCollection }));
        });
    };
    return React.createElement(View, null, renderTiles());
};
export default CollectionBrowser;
